import PageButton from '../../components/button.vue';

export default {
	components: {
		PageButton,
	},

	data() {
		return {
			temp:{
				detail:'',
				picUrl:'',
				realName:'',
				idCard:"",
				gender:"",
				weight:'',
				allergic:'',
				conceive:'',
				liver:'',
				kidney:'',
				medicalHistory:'',
				payType:''
			},
			conceive:[{"key":"-1","value":"请选择"},{"key":"0","value":"无备孕计划"},{"key":"1","value":"备孕中"},{"key":"2","value":"怀孕中"},{"key":"3","value":"哺乳期"}],
			index:-1
	
		}
	},
	onLoad(options) {
		this.temp = JSON.parse(options.temp);
	},
	onShow() {
		
	},
	methods: {
		genderChange:function(e){
			this.temp.gender = e.target.value
		},
		allergicChange:function(e){
			this.temp.allergic = e.target.value
		},
		liverChange:function(e){
			this.temp.liver = e.target.value
		},
		kidneyChange:function(e){
			this.temp.kidney = e.target.value
		},
		bindConceiveChange: function(e) {
			let that = this;
			that.index = e.target.value;
			that.temp.conceive = that.conceive[that.index].key;
		},
		payTypeChang:function(e){
			this.temp.payType = e.target.value;
		},
		submit:function(){
			let that = this;
			if(!that.$util.isEmpty(that.temp.realName)){
				uni.showToast({
					title: '请输入真实姓名',
					icon:'none',
					duration: 2000
				});
				return;
			}
			if(!that.$util.isEmpty(that.temp.idCard)){
				uni.showToast({
					title: '请输入身份证号',
					icon:'none',
					duration: 2000
				});
				return;
			}
			if(!that.$util.isEmpty(that.temp.gender)){
				uni.showToast({
					title: '请选择性别',
					icon:'none',
					duration: 2000
				});
				return;
			}
			if(!that.$util.isEmpty(that.temp.payType)){
				uni.showToast({
					title: '请选择支付方式',
					icon:'none',
					duration: 2000
				});
				return;
			}
			that.$util.request({
				url: "/didi-patient/inquiryinfo/add" ,
				param: that.temp,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					that.payInquiry(res.data.inquiryId);
				},
				error: function() {}
			})
		},
		
		payInquiry:function(inquiryId){
			let that = this;
			var temp = {};
			temp.inquiryId = inquiryId;
			that.$util.request({
				url: "/didi-patient/patientpayinfo/unifiedOrder",
				param: temp,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					var data = res.data;
					 //调用微信支付
					uni.requestPayment({
					  'appId': data.appId,
					  'timeStamp': data.timeStamp,
					  'nonceStr': data.nonceStr,
					  'package': data.package,
					  'signType': data.signType,
					  'paySign': data.paySign,
					  'success': function (res) {
					    uni.showToast({
					    	title: '下单成功',
					    	icon:'none',
					    	duration: 2000
					    });
						uni.redirectTo({
							url:'../my/userInquiry'
						})
					  },
					  'fail': function (res) {
						 uni.redirectTo({
						 	url:'../my/userInquiry'
						 })
					  }
					})
				},
				error: function() {}
			})
		}
		
		
	}
}
